import $ from 'jquery'
import Vue from 'vue'
import {Notification} from 'element-ui'
import {lowerCamel, lowerSlash, startWith, toPlural} from '../../filter/str'
import {str2Date} from '../../filter/time'

export default class Base {

  constructor(args) {
    //local fields. Used in UI.
    this.errorMessage = null
    this.editMode = false
    this.loading = false

    //loadingloadingLoadingFrame
    this.detailLoading = false
  }

  getStatusList() {

    if (!this.StatusMap) {
      console.error(this.getTAG() + 'StatusMap!')
      return []
    }

    let list = []
    for (let key in this.StatusMap) {

      if (this.StatusMap.hasOwnProperty(key)) {
        list.push(this.StatusMap[key])
      }

    }

    if (list.length === 0) {
      console.error(this.getTAG() + 'StatusList')
    }

    return list

  }

  getStatusMap() {
    if (!this.StatusMap) {
      console.error(this.getTAG() + 'StatusMap!')
      return {}
    } else {
      return this.StatusMap
    }

  }

  getStatusItem(status) {
    if (!this.StatusMap) {
      console.error(this.getTAG() + 'StatusMap!')
      return {
        name: '',
        value: null,
        style: 'danger',
        icon: 'ban'
      }
    } else {
      let item = this.StatusMap[status]
      if (item) {
        return item
      } else {
        return {
          name: '',
          value: null,
          style: 'danger',
          icon: 'ban'
        }
      }
    }
  }

  getStatusName() {
    if (this.status && this.StatusMap) {
      let item = this.StatusMap[this.status]
      if (item) {
        return item.name
      }

      console.error(' ' + this.status)
    } else {
      console.error(' status')

    }

    return ''
  };

  getStatusStyle() {

    if (this.status && this.StatusMap) {
      let item = this.StatusMap[this.status]
      if (item) {
        return item.style
      }
      console.error(this.getTAG() + ' ' + this.status)
    } else {
      console.error(this.getTAG() + ' status')
    }

    return 'default'
  };

  getStatusIcon() {

    if (this.status && this.StatusMap) {
      let item = this.StatusMap[this.status]
      if (item) {
        return item.icon
      }
      console.error(this.getTAG() + ' ' + this.status)
    } else {
      console.error(this.getTAG() + ' status')
    }

    return 'ban'
  };

  getTypeList() {

    if (!this.TypeMap) {
      console.error(this.getTAG() + 'TypeMap!')
      return []
    }

    let list = []
    for (let key in this.TypeMap) {

      if (this.TypeMap.hasOwnProperty(key)) {
        list.push(this.TypeMap[key])
      }

    }

    if (list.length === 0) {
      console.error(this.getTAG() + ' TypeList')
    }

    return list

  }

  getTypeMap() {
    if (!this.TypeMap) {
      console.error(this.getTAG() + 'TypeMap!')
      return {}
    } else {
      return this.TypeMap
    }

  }

  getTypeItem(type) {
    if (!this.TypeMap) {
      console.error(this.getTAG() + 'TypeMap!')
      return {
        name: '',
        value: null,
        style: 'danger',
        icon: 'ban'
      }
    } else {
      let item = this.TypeMap[type]
      if (item) {
        return item
      } else {
        return {
          name: '',
          value: null,
          style: 'danger',
          icon: 'ban'
        }
      }
    }
  }

  getTypeName() {
    if (this.type && this.TypeMap) {
      let item = this.TypeMap[this.type]
      if (item) {
        return item.name
      }

      console.error(' ' + this.type)
    } else {
      console.error(' type')
    }

    return ''
  };

  getTypeStyle() {
    if (this.type && this.TypeMap) {
      let item = this.TypeMap[this.type]
      if (item) {
        return item.style
      }

      console.error(' ' + this.type)
    } else {

      console.error(' type')
    }

    return 'default'
  };

  getTypeIcon() {
    if (this.type && this.TypeMap) {
      let item = this.TypeMap[this.type]
      if (item) {
        return item.icon
      }

      console.error(' ' + this.type)
    } else {

      console.error(' type')
    }

    return 'default'
  };

  //StatusClazzthis
  static registerStatusEnum(StatusMap) {

    let Clazz = this
    let Status = {}
    let StatusList = []
    for (let key in StatusMap) {
      if (StatusMap.hasOwnProperty(key)) {
        Status[key] = key
        StatusList.push(StatusMap[key])
      }
    }
    Clazz.prototype.Status = Status
    Clazz.prototype.StatusList = StatusList
    Clazz.prototype.StatusMap = StatusMap
  }

  //TypeClazzthis
  static registerTypeEnum(TypeMap) {
    let Clazz = this
    let Type = {}
    let TypeList = []
    for (let key in TypeMap) {
      if (TypeMap.hasOwnProperty(key)) {
        Type[key] = key
        TypeList.push(TypeMap[key])
      }
    }
    Clazz.prototype.Type = Type
    Clazz.prototype.TypeList = TypeList
    Clazz.prototype.TypeMap = TypeMap
  }

  //prototypeCategory
  //Category CategoryMap CategoryList getCategoryList() getCategoryMap()
  // getCategoryItem() getCategoryStyle() getCategoryName() getCategoryIcon()
  static registerEnum(EnumName, EnumMap) {
    let Clazz = this
    if (!EnumName || !EnumMap) {
      console.error('')
      return
    }

    //
    let enumName = EnumName.replace(/(\w)/, function (v) {
      return v.toLowerCase()
    })
    let Enum = {}
    let EnumList = []
    for (let key in EnumMap) {
      let item = EnumMap[key]
      Enum[key] = item.value
      EnumList.push(EnumMap[key])
    }

    Clazz.prototype[EnumName] = Enum

    Clazz.prototype[EnumName + 'Map'] = EnumMap
    Clazz.prototype['get' + EnumName + 'Map'] = function () {
      return EnumMap
    }
    Clazz.prototype[EnumName + 'List'] = EnumList
    Clazz.prototype['get' + EnumName + 'List'] = function () {
      return EnumList
    }
    Clazz.prototype['get' + EnumName + 'Item'] = function () {
      let itemValue = this[enumName]
      if (itemValue !== null && typeof itemValue !== "undefined") {

        let item = null

        for (let k in EnumMap) {

          if (EnumMap.hasOwnProperty(k)) {
            let temp = EnumMap[k];
            if (temp.value === itemValue) {
              item = temp
              break
            }
          }

        }

        if (item) {
          return item
        }
        console.error(' ' + itemValue)
      } else {

        console.error(' enum')
      }
      return {
        name: '',
        value: null,
        style: 'danger',
        icon: 'ban'
      }
    }
    Clazz.prototype['get' + EnumName + 'Style'] = function () {


      let itemValue = this[enumName]
      if (itemValue !== null && typeof itemValue !== "undefined") {


        let item = null

        for (let k in EnumMap) {

          if (EnumMap.hasOwnProperty(k)) {
            let temp = EnumMap[k];
            if (temp.value === itemValue) {
              item = temp
              break
            }
          }

        }

        if (item) {
          return item.style
        }

        console.error(' ' + itemValue)
      } else {

        console.error(' enum')
      }

      return 'default'
    }
    Clazz.prototype['get' + EnumName + 'Name'] = function () {

      let itemValue = this[enumName]


      if (itemValue !== null && typeof itemValue !== "undefined") {
        let item = null

        for (let k in EnumMap) {

          if (EnumMap.hasOwnProperty(k)) {
            let temp = EnumMap[k];
            if (temp.value === itemValue) {
              item = temp
              break
            }
          }

        }


        if (item) {
          return item.name
        }

        console.error(' ' + itemValue)
      } else {

        console.error(' enum')
      }

      return ''
    }
    Clazz.prototype['get' + EnumName + 'Icon'] = function () {
      let itemValue = this[enumName]
      if (itemValue !== null && typeof itemValue !== "undefined") {

        let item = null

        for (let k in EnumMap) {

          if (EnumMap.hasOwnProperty(k)) {
            let temp = EnumMap[k];
            if (temp.value === itemValue) {
              item = temp
              break
            }
          }

        }

        if (item) {
          return item.icon
        }

        console.error(' ' + itemValue)
      } else {

        console.error(' enum')
      }

      return 'ban'
    }

    return 'default'
  };

  //We use this method to get the full js Object.
  // one2one one2one
  render(obj, one2one = false) {
    if (obj) {
      $.extend(this, obj)
    }
  }

  //
  //SpaceApplySpaceSeatsrender
  simpleRender(obj, one2one = false) {
    this.render(obj, one2one)
  }

  /**
   *
   * @param field 
   * @param Clazz 
   * @param simpleRender 
   */
  renderList(field, Clazz, simpleRender = true) {

    let beans = this[field]
    if (!beans) {
      //
      this[field] = (new this.constructor())[field]
      return
    }

    if (!Clazz) {
      return
    }

    this[field] = []

    for (let i = 0; i < beans.length; i++) {
      let bean = beans[i]
      let clazz = new Clazz()

      if (simpleRender) {
        clazz.simpleRender(bean)
      } else {
        clazz.render(bean)
      }

      this[field].push(clazz)
    }
  }

  //renderEntity. fieldClazz
  renderEntity(field, Clazz, one2one = false) {

    let obj = this[field]
    if (!obj) {
      if (Clazz) {
        let EntityClazz = this.constructor
        obj = (new EntityClazz())[field]

      } else {
        return
      }
    }

    if (Clazz === Date) {

      this[field] = str2Date(obj)
    } else if (Clazz.prototype instanceof Base) {



      //
      //1024*1024  "1M"let bean = new Clazz(); renderavatarObject.
      let bean = (new this.constructor())[field]
      if (!bean) {
        bean = new Clazz()
      }

      if (obj !== null) {
        bean.render(obj, one2one)
        this[field] = bean
      }

    } else {
      console.error('')
    }

  }

  //we provide a default error handing method. handle with specific errorCallback.
  defaultErrorHandler(response, errorCallback) {

    let msg = this.getErrorMessage(response)

    if (typeof errorCallback === 'function') {
      errorCallback(response)
    } else {
      Notification.error({
        title: '',
        message: msg
      })
    }
  }

  //return true ->   false ->  
  loginErrorHandler(response) {

    let temp = response['data']
    if (temp !== null && typeof temp === 'object') {
      if (temp['code'] === -400) {

        //
        if (Vue.store.state.route.path === "/by/user/login") {
          return true
        }
        //
        if ((new Date().getTime()) - Vue.store.state.lastLoginErrorTimestamp < 3000) {
          return true
        } else {
          Vue.store.state.lastLoginErrorTimestamp = (new Date().getTime());
        }



        Notification.error({
          message: ''
        })

        //
        Vue.store.state.user.innerLogout()

        Vue.router.push({
          path: '/by/user/login',
          query: {redirect: Vue.store.state.route.fullPath}
        })

        return true

      }
    }

    return false

  }

  //get errorMessage from response and wrap the value to this.errorMessage.
  getErrorMessage(response) {

    let msg = '!'

    if (response === null) {
      msg = ''
    } else if (typeof response === 'string') {
      msg = response
    } else if (response['msg']) {
      msg = response['msg']
    } else if (response['message']) {
      msg = response['message']
    } else {
      let temp = response['data']
      if (temp !== null && typeof temp === 'object') {
        if (temp['message']) {
          msg = temp['message']
        } else if (temp['msg']) {
          msg = temp['msg']
        } else {
          if (temp['error'] && temp['error']['message']) {
            msg = temp['error']['message']
          }
        }
      }
    }
    this.errorMessage = msg
    return msg
  }

  //Vue.http.get('/someUrl', [options]).then(successCallback, errorCallback);
  //optshttps://github.com/pagekit/vue-resource/blob/develop/docs/http.md
  httpGet(url, params = {}, successCallback, errorCallback, opts = {}) {

    let that = this
    let fullUrl = url
    if (!startWith(url, 'http')) {
      fullUrl = Vue.http.options.root + url
    }

    let options = $.extend({}, opts)
    options['params'] = params

    this.loading = true
    Vue.http.get(fullUrl, options).then(function (response) {

      that.loading = false;
      (typeof successCallback === 'function') && successCallback(response)

    }, function (response) {

      that.loading = false

      console.error(response)
      //errorMessageuser httpLogout
      //that.errorMessage = that.getErrorMessage(response)

      //
      if (that.loginErrorHandler(response)) {
        return
      }

      //
      if (typeof errorCallback === 'function') {
        errorCallback(response)
      } else {
        //toast
        that.defaultErrorHandler(response)
      }

    })

  }

  //Vue.http.post('/someUrl', [body], [options]).then(successCallback, errorCallback);
  //url is something like this: /article/detail/1
  //optshttps://github.com/pagekit/vue-resource/blob/develop/docs/http.md
  httpPost(url, params, successCallback, errorCallback, opts = {}) {
    let that = this

    let fullUrl = url
    if (!startWith(url, 'http')) {
      fullUrl = Vue.http.options.root + url
    }

    let options = $.extend({}, opts)

    //options["emulateJSON"] = !(params instanceof FormData);

    //Postjson
    options['emulateJSON'] = true

    this.loading = true
    Vue.http.post(fullUrl, params, options).then(function (response) {
      that.loading = false

      typeof successCallback === 'function' && successCallback(response)

    }, function (response) {
      that.loading = false

      console.error(response)
      //errorMessageuser httpLogout
      //that.errorMessage = that.getErrorMessage(response)

      //
      if (that.loginErrorHandler(response)) {
        return
      }

      //
      if (typeof errorCallback === 'function') {
        errorCallback(response)
      } else {
        //toast
        that.defaultErrorHandler(response)
      }

    })

  }

  // Project project
  getTAG() {

    let className = this.constructor.name

    return lowerCamel(className)
  }

  // Project projects
  getTAGS() {

    return toPlural(this.getTAG())
  }

  //url
  getUrlPrefix() {
    return lowerSlash(this.getTAG())
  }

}



