import Vue from 'vue'
import Vuex from 'vuex'
import User from "../model/user/User";
import Preference from '../model/preference/Preference'

Vue.use(Vuex)

let user = new User()
user.renderFromLocalStorage()

const state = {
  config: {
    mobile: false,
    showDrawer: true
  },
  debug: true,

  host: '/api',

  //
  version: {
    identifier: 'cn.zicla.blog',
    platform: 'WEB',
    versionCode: '1',
    versionName: '1.0'
  },

  //
  user,

  breadcrumbs: [],

  //
  preference: new Preference(),
  //
  lastLoginErrorTimestamp: 0

}

const getters = {
  getConfig(state) {
    return state.config
  }
}

const mutations = {}

const actions = {}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



