	_e(function (E, $) {

		E.createMenu(function (check) {
			let menuId = 'quote';
			if (!check(menuId)) {
				return;
			}
			let editor = this;
			let lang = editor.config.lang;

			//  menu 
			let menu = new E.Menu({
				editor: editor,
				id: menuId,
				title: lang.quote,
				commandName: 'formatBlock',
				commandValue: 'blockquote'
			});

			// click
			menu.clickEvent = function (e) {
				let rangeElem = editor.getRangeElem();
				let $rangeElem;
				if (!rangeElem) {
					e.preventDefault();
					return;
				}
				let currentQuote = editor.getSelfOrParentByName(rangeElem, 'blockquote');
				let $quote;

				if (currentQuote) {
					// quote
					e.preventDefault();
					return;
				}

				rangeElem = editor.getLegalTags(rangeElem);
				$rangeElem = $(rangeElem);

				// 
				if (!$rangeElem.text()) {
					return;
				}


				if (!rangeElem) {
					// 
					// IE8 
					editor.command(e, 'formatBlock', 'blockquote');
					return;
				}

				// command
				function commandFn() {
					$quote = $('<p>' + $rangeElem.text() + '</p>');
					$rangeElem.after($quote).remove();
					$quote.wrap('<blockquote>');
				}

				//  callback 
				function callback() {
					// callbackrangequote
					let editor = this;
					if ($quote) {
						editor.restoreSelectionByElem($quote.get(0));
					}
				}

				// 
				editor.customCommand(e, commandFn, callback);
			};

			// click
			menu.clickEventSelected = function (e) {
				let rangeElem;
				let quoteElem;
				let $lastChild;

				// elem quote 
				rangeElem = editor.getRangeElem();
				quoteElem = editor.getSelfOrParentByName(rangeElem, 'blockquote');
				if (!quoteElem) {
					// 
					e.preventDefault();
					return;
				}

				// command
				function commandFn() {
					let $quoteElem;
					let $children;

					$quoteElem = $(quoteElem);
					$children = $quoteElem.children();
					if ($children.length) {
						$children.each(function (k) {
							let $item = $(this);
							if ($item.get(0).nodeName === 'P') {
								$quoteElem.after($item);
							} else {
								$quoteElem.after('<p>' + $item.text() + '</p>');
							}
							$lastChild = $item;  // callbackrange
						});
						$quoteElem.remove();
						return;
					}
				}

				// callback
				function callback() {
					// callbackrangelastChild
					let editor = this;
					if ($lastChild) {
						editor.restoreSelectionByElem($lastChild.get(0));
					}
				}

				// 
				editor.customCommand(e, commandFn, callback);
			};

			// 
			menu.updateSelectedEvent = function () {
				let self = this; //
				let editor = self.editor;
				let rangeElem;

				rangeElem = editor.getRangeElem();
				rangeElem = editor.getSelfOrParentByName(rangeElem, 'blockquote');

				if (rangeElem) {
					return true;
				}

				return false;
			};

			// editor
			editor.menus[menuId] = menu;

			// ---------------  enter  ---------------
			editor.ready(function () {
				let editor = this;
				let $txt = editor.txt.$txt;
				let isPrevEnter = false;  // quote enter 
				$txt.on('keydown', function (e) {
					if (e.keyCode !== 13) {
						//  enter 
						isPrevEnter = false;
						return;
					}

					let rangeElem = editor.getRangeElem();
					rangeElem = editor.getSelfOrParentByName(rangeElem, 'blockquote');
					if (!rangeElem) {
						//  quote
						isPrevEnter = false;
						return;
					}

					if (!isPrevEnter) {
						// qoteenter
						isPrevEnter = true;
						return;
					}

					let currentRangeElem = editor.getRangeElem();
					let $currentRangeElem = $(currentRangeElem);
					if ($currentRangeElem.length) {
						$currentRangeElem.parent().after($currentRangeElem);
					}

					// 
					editor.restoreSelectionByElem(currentRangeElem, 'start');

					isPrevEnter = false;
					// 
					e.preventDefault();

				});
			}); // editor.ready(

			// --------------- quote ---------------
			editor.ready(function () {
				let editor = this;
				let $txt = editor.txt.$txt;
				let $rangeElem;

				function commandFn() {
					$rangeElem && $rangeElem.remove();
				}

				function callback() {
					if (!$rangeElem) {
						return;
					}
					let $prev = $rangeElem.prev();
					if ($prev.length) {
						//  prev  prev 
						editor.restoreSelectionByElem($prev.get(0));
					} else {
						//  prev 
						editor.initSelection();
					}
				}

				$txt.on('keydown', function (e) {
					if (e.keyCode !== 8) {
						//  backspace 
						return;
					}

					let rangeElem = editor.getRangeElem();
					rangeElem = editor.getSelfOrParentByName(rangeElem, 'blockquote');
					if (!rangeElem) {
						//  quote
						return;
					}
					$rangeElem = $(rangeElem);

					let text = $rangeElem.text();
					if (text) {
						// quote 
						return;
					}
					editor.customCommand(e, commandFn, callback);

				}); // $txt.on
			}); // editor.ready(
		});

	});
