// xhr 
	_e(function (E, $) {

		if (!window.FileReader || !window.FormData) {
			// html5
			return;
		}

		E.plugin(function () {

			let editor = this;
			let config = editor.config;
			let uploadImgUrl = config.uploadImgUrl;
			let uploadTimeout = config.uploadTimeout;

			// 
			let uploadImgFns = config.uploadImgFns;
			let onload = uploadImgFns.onload;
			let ontimeout = uploadImgFns.ontimeout;
			let onerror = uploadImgFns.onerror;

			if (!uploadImgUrl) {
				return;
			}

			// -------- base64urlBlob --------
			function convertBase64UrlToBlob(urlData, filetype) {
				//urlbyte
				let bytes = window.atob(urlData.split(',')[1]);

				//,ascii00
				let ab = new ArrayBuffer(bytes.length);
				let ia = new Uint8Array(ab);
				let i;
				for (i = 0; i < bytes.length; i++) {
					ia[i] = bytes.charCodeAt(i);
				}

				return new Blob([ab], {type: filetype});
			}

			// --------  --------
			function insertImg(src, event) {
				let img = document.createElement('img');
				img.onload = function () {
					let html = '<img src="' + src + '" style="max-width:100%;"/>';
					editor.command(event, 'insertHtml', html);

					E.log(' ' + src);
					img = null;
				};
				img.onerror = function () {
					E.error('' + src);
					img = null;
				};
				img.src = src;
			}

			// -------- onprogress  --------
			function updateProgress(e) {
				if (e.lengthComputable) {
					let percentComplete = e.loaded / e.total;
					editor.showUploadProgress(percentComplete * 100);
				}
			}

			// -------- xhr  --------
			editor.xhrUploadImg = function (opt) {
				// opt 
				let event = opt.event;
				let fileName = opt.filename || '';
				let base64 = opt.base64;
				let fileType = opt.fileType || 'image/png'; //  png
				let name = opt.name || 'wangEditor_upload_file';
				let loadfn = opt.loadfn || onload;
				let errorfn = opt.errorfn || onerror;
				let timeoutfn = opt.timeoutfn || ontimeout;

				//  token
				let params = editor.config.uploadParams || {};

				// headers
				let headers = editor.config.uploadHeaders || {};

				// 
				let fileExt = 'png';  //  png
				if (fileName.indexOf('.') > 0) {
					// 
					fileExt = fileName.slice(fileName.lastIndexOf('.') - fileName.length + 1);
				} else if (fileType.indexOf('/') > 0 && fileType.split('/')[1]) {
					//  'image/png'  'png'
					fileExt = fileType.split('/')[1];
				}

				// ------------ begin  ------------
				if (E.isOnWebsite) {
					E.log('');
					insertImg(base64, event);
					return;
				}
				// ------------ end  ------------

				// 
				let xhr = new XMLHttpRequest();
				let timeoutId;
				let src;
				let formData = new FormData();

				// 
				function timeoutCallback() {
					if (timeoutId) {
						clearTimeout(timeoutId);
					}
					if (xhr && xhr.abort) {
						xhr.abort();
					}

					// 
					event.preventDefault();

					// 
					timeoutfn && timeoutfn.call(editor, xhr);

					// 
					editor.hideUploadProgress();
				}

				xhr.onload = function () {
					if (timeoutId) {
						clearTimeout(timeoutId);
					}

					//  editor.uploadImgOriginalName  alt 
					editor.uploadImgOriginalName = fileName;
					if (fileName.indexOf('.') > 0) {
						editor.uploadImgOriginalName = fileName.split('.')[0];
					}

					// loadload
					loadfn && loadfn.call(editor, xhr.responseText, xhr);

					// 
					editor.hideUploadProgress();
				};
				xhr.onerror = function () {
					if (timeoutId) {
						clearTimeout(timeoutId);
					}

					// 
					event.preventDefault();

					// errorerror
					errorfn && errorfn.call(editor, xhr);

					// 
					editor.hideUploadProgress();
				};
				// xhr.onprogress = updateProgress;
				xhr.upload.onprogress = updateProgress;

				// 
				formData.append(name, convertBase64UrlToBlob(base64, fileType), E.random() + '.' + fileExt);

				// 
				$.each(params, function (key, value) {
					formData.append(key, value);
				});

				// 
				xhr.open('POST', uploadImgUrl, true);
				// xhr.setRequestHeader("Content-type","application/x-www-form-urlencoded");  // form

				// headers
				$.each(headers, function (key, value) {
					xhr.setRequestHeader(key, value);
				});

				// cookie
				xhr.withCredentials = true;

				// 
				xhr.send(formData);
				timeoutId = setTimeout(timeoutCallback, uploadTimeout);

				E.log('...');
			};
		});
	});
