/**
 * 
 *
 */
import BaseEntity from './BaseEntity'
import { isEmptyObject } from '../../util/Utils'

let Type = {
  //boolean
  CHECK: 'CHECK',
  //
  INPUT: 'INPUT',
  //
  SORT: 'SORT',
  //
  SELECTION: 'SELECTION',
  //status = RUNNING or status = STOP
  MULTI_SELECTION: 'MULTI_SELECTION',
  //Pager
  HTTP_SELECTION: 'HTTP_SELECTION',
  //
  HTTP_INPUT_SELECTION: 'HTTP_INPUT_SELECTION',
  //
  DATE_TIME_SELECTION: 'DATE_TIME_SELECTION'
}

export default class Filter {

  constructor (type, name, key, options, Clazz, visible = true, component) {
    if (!type || !name || !key) {
      console.error('Filter ' + type + ' ' + name + ' ' + key)
      return
    }

    // 
    this.type = type
    //
    this.name = name
    //
    this.key = key
    // ->   -> ASC/DESC/null  -> OK/ERROR/RUNNING
    this.value = null

    //
    this.active = -1

    this.options = options

    this.Clazz = Clazz

    //Filter
    this.visible = visible

    //
    this.component = component

    //HTTP_SELECTION
    this.initFilter = {}

    if (type === Type.SELECTION) {
      if (!options || !(options instanceof Array)) {
        console.error('Filter SELECTION  optionsnamevaluestyle')

      }
    } else if (type === Type.MULTI_SELECTION) {
      if (!options || !(options instanceof Array)) {
        console.error('Filter MULTI_SELECTION  optionsnamevaluestyle')

      } else {
        this.value = []
      }
    } else if (type === Type.HTTP_SELECTION) {
      if (!Clazz || !(Clazz.prototype instanceof BaseEntity)) {
        console.error('ClazzBaseEntity')

      }
    }

  }

  isEmpty () {

    if (this.type === Type.MULTI_SELECTION) {
      return isEmptyObject(this.value)
    } else if (this.type === Type.CHECK) {
      return this.value === null
    } else {
      return !this.value
    }
  };

  reset () {
    this.value = null
    this.active = -1
  };

  putValue (value) {
    if (value === null) {
      this.value = null
      return
    }

    if (this.type === Type.MULTI_SELECTION) {

      let draftArray = null
      if (value instanceof Array) {
        draftArray = value
      } else {
        draftArray = value.split(',')
      }
      let arr = []
      for (let i = 0; i < draftArray.length; i++) {
        let item = draftArray[i]

        //options
        for (let j = 0; j < this.options.length; j++) {
          let opt = this.options[j]
          if (opt.value === item) {
            //
            if (arr.indexOf(item) === -1) {
              arr.push(item)
            }
            break
          }
        }
      }
      this.value = arr
    } else if (this.type === this.Type.SELECTION) {

      for (let j = 0; j < this.options.length; j++) {

        let opt = this.options[j]
        if (opt.value === value) {
          this.value = value
          this.active = j
        }

      }
    } else {
      this.value = value
    }

  };

  //filtervalueparamsparamshttp
  getParam () {

    if (this.type === Type.MULTI_SELECTION) {
      if (this.value && this.value.length) {
        return this.value.toString()
      } else {
        return null
      }
    } else {
      return this.value
    }

  };

}

Filter.prototype.Type = Type



