import BaseEntity from '../base/BaseEntity'
import Filter from '../base/Filter'
import $ from 'jquery'
import { readLocalStorage, removeLocalStorage, saveToLocalStorage } from '../../util/Utils'
import MenuManager from '../../frontend/MenuManager'
import Tank from '../tank/Tank'
import { FeatureType } from '../feature/FeatureType'
import { Notification } from 'element-ui'

let Gender = {
  UNKNOWN: 'UNKNOWN',
  MALE: 'MALE',
  FEMALE: 'FEMALE'
}
let GenderMap = {
  UNKNOWN: {
    name: '',
    value: 'UNKNOWN',
    style: 'success'
  },
  MALE: {
    name: '',
    value: 'MALE',
    style: 'info'
  },
  FEMALE: {
    name: '',
    value: 'FEMALE',
    style: 'primary'
  }
}

let Role = {
  GUEST: 'GUEST',
  USER: 'USER',
  ADMIN: 'ADMIN'
}
let RoleMap = {
  GUEST: {
    name: '',
    value: 'GUEST',
    style: 'primary'
  },
  USER: {
    name: '',
    value: 'USER',
    style: 'success'
  },
  ADMIN: {
    name: '',
    value: 'ADMIN',
    style: 'info'
  }
}

export default class User extends BaseEntity {
  constructor (args) {
    super(args)

    this.username = null
    this.password = null
    this.role = Role.USER
    this.email = null
    this.phone = null
    //
    this.gender = Gender.UNKNOWN
    this.city = null
    this.description = null
    this.avatarTankUuid = null
    this.avatarUrl = null
    //IP
    this.lastIp = null
    //
    this.lastTime = null

    //
    this.emailValidate = false

    this.avatar = new Tank('image', false, 1024 * 1024, '1M')

    //
    this.articleNum = 0

    //
    this.articleAgreeNum = 0

    //
    this.articleWords = 0

    //
    this.articleHit = 0

    //
    this.commentNum = 0

    //local fields.
    this.byMenus = []

    //
    this.localPassword = null
    this.createTime = null

    //
    this.validatorSchema = {
      username: {
        rules: [{required: true, message: ''}],
        error: null
      },
      password: {
        rules: [{required: true, message: ''}],
        error: null
      },
      role: {
        rules: [{required: true, message: ''}],
        error: null
      },
      email: {
        rules: [{required: true, message: ''}, {
          type: 'string',
          pattern: /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/,
          message: ''
        }],
        error: null
      },
      gender: {
        rules: [{required: true, message: ''}],
        error: null
      }

    }

  }

  static URL_LOGIN = '/user/login'
  static URL_LOGOUT = '/user/logout'
  static URL_USER_CHANGE_PASSWORD = '/user/change/password'
  static URL_USER_RESET_PASSWORD = '/user/reset/password'
  static URL_USER_EMAIL_SEND = '/user/email/send'

  static getLegalRoleList(){
    let getLegalRoleList = []
    for (let key in RoleMap) {
      if (RoleMap.hasOwnProperty(key) && key !== 'GUEST') {
        getLegalRoleList.push(RoleMap[key]);
      }
    }
    return getLegalRoleList
  }


  getFilters () {
    return [
      new Filter(Filter.prototype.Type.SORT, '', 'orderSort'),
      new Filter(Filter.prototype.Type.SORT, '', 'orderLastTime'),
      new Filter(Filter.prototype.Type.INPUT, '', 'username'),
      new Filter(Filter.prototype.Type.INPUT, '', 'email'),
      new Filter(Filter.prototype.Type.INPUT, '', 'phone'),
      new Filter(Filter.prototype.Type.SELECTION, '', 'role', User.getLegalRoleList()),
      new Filter(Filter.prototype.Type.INPUT, '', 'keyword')
    ]
  };

  //We use this method to get the full js Object.
  render (obj) {
    super.render(obj)
    this.renderEntity('lastTime', Date)
    this.renderEntity('avatar', Tank)
  }




  //url.
  getAvatarUrl () {
    if (this.avatarUrl) {
      return this.avatarUrl
    } else {
      return require('../../../assets/img/avatar.png')
    }
  }

  refreshMenus () {

    //
    this.byMenus = MenuManager.refreshByMenus(this)

  };

  //
  renderFromLocalStorage () {

    try {
      let userString = readLocalStorage(this.getTAG())

      if (userString) {
        let json = JSON.parse(userString)
        this.render(json)

      }

    } catch (e) {
      removeLocalStorage(this.getTAG())
    }
    //user
    this.refreshMenus()
  }

  //
  saveToLocalStorage (rawUserObject = null) {

    saveToLocalStorage(this.getTAG(), JSON.stringify(rawUserObject))
  }

  //user
  clearLocalStorage () {

    removeLocalStorage(this.getTAG())
  }

  //
  updateLocalStorage (opt = {}) {
    try {
      let userString = readLocalStorage(this.getTAG())

      if (userString) {
        let json = JSON.parse(userString)
        $.extend(json, opt)

        saveToLocalStorage(this.getTAG(), JSON.stringify(json))
      }

    } catch (e) {
      removeLocalStorage(this.getTAG())
    }
  }

  getForm () {

    return {
      username: this.username,
      password: this.password,
      role: this.role,
      email: this.email,
      phone: this.phone,
      gender: this.gender,
      city: this.city,
      description: this.description,
      avatarTankUuid: this.avatar.uuid,
      avatarUrl: this.avatar.uuid ? this.avatar.url : null,
      uuid: this.uuid ? this.uuid : null
    }
  }

  validate () {

    if (this.editMode) {
      this.password = true
    }

    return super.validate()
  }

  httpSave (successCallback, errorCallback) {
    let that = this
    let url = this.getUrlCreate()
    if (this.uuid) {
      url = this.getUrlEdit()
    }

    if (!this.validate()) {
      that.defaultErrorHandler(this.errorMessage, errorCallback)
      return
    }

    let form = this.getForm()

    this.httpPost(url, form, function (response) {

      that.render(response.data.data)

      successCallback && successCallback(response)

    }, errorCallback)
  }

  //local logout.
  innerLogout () {

    this.render(new User())

    //
    /*this.refreshMenus()*/

    this.clearLocalStorage()

  }

  innerLogin (response) {
    let that = this
    that.errorMessage = null
    that.render(response.data.data)

    //
    this.refreshMenus()

    //
    this.saveToLocalStorage(response.data.data)

  }

  resetValidate () {

    if (!this.phone) {
      this.errorMessage = ''
      return false
    }
    if (!this.password) {
      this.errorMessage = ''
      return false
    }

    return true
  }

  hasPermission (featureType) {
    if (this.role === Role.ADMIN) {
      return true
    } else if (this.role === Role.USER) {
      return featureType === FeatureType.PUBLIC || featureType === FeatureType.USER_MINE
    } else {
      return featureType === FeatureType.PUBLIC
    }

  }

  getResetForm () {
    return {
      phone: this.phone,
      password: this.password
    }
  }

  httpLogin (captcha, successCallback, errorCallback) {

    let that = this

    //
    if (!this.email) {
      this.errorMessage = ''
      return
    }
    if (!this.localPassword) {
      this.errorMessage = ''
      return
    }
    this.errorMessage = null

    let form = {
      email: this.email,
      password: this.localPassword
    }

    if (captcha) {
      form.captcha = captcha
    }

    this.httpPost(User.URL_LOGIN, form, function (response) {

      that.innerLogin(response)

      successCallback && successCallback(response)
    }, function (response) {

      that.errorMessage = that.getErrorMessage(response)
      errorCallback && errorCallback(response)
    })
  }

  httpResetPassword (captcha, successCallback, errorCallback) {
    let that = this

    if (!this.resetValidate()) {
      return
    }

    let form = this.getResetForm()

    if (captcha) {
      form.captcha = captcha
    }

    this.httpPost(User.URL_MEMBER_RESET_PASSWORD, form, function (response) {

      successCallback && successCallback(response)
    }, errorCallback)

  }

  httpLogout (successCallback, errorCallback) {

    let that = this

    that.innerLogout()

    this.httpPost(User.URL_LOGOUT, {}, function (response) {

      successCallback && successCallback(response)
    }, errorCallback)
  }

  httpUserChangePassword (oldPassword, newPassword, successCallback, errorCallback) {
    let that = this
    this.httpPost(User.URL_USER_CHANGE_PASSWORD, {
      'oldPassword': oldPassword,
      'newPassword': newPassword
    }, function (response) {
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpUserResetPassword (newPassword, successCallback, errorCallback) {
    let that = this
    this.httpPost(User.URL_USER_RESET_PASSWORD, {
      'userUuid': this.uuid,
      'newPassword': newPassword
    }, function (response) {
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpConfirmEmail (successCallback, errorCallback) {
    let that = this
    this.httpPost(User.URL_USER_EMAIL_SEND,{},function (response) {
      Notification.success({
        message: ''
      })
      typeof successCallback === 'function' && successCallback(response)
    },errorCallback)
  }

}

//Gender
User.registerEnum('Gender', GenderMap)
User.registerEnum('Role', RoleMap)





