//
/** * Date Date String * (M)(d)12(h)24(H)(m)(s)(E)(q)
  1-2  * (y) 1-4 (S) 1 ( 1-3 ) * eg: * (new
 Date()).pattern("yyyy-MM-dd hh:mm:ss.S")==> 2006-07-02 08:09:04.423
 * (new Date()).pattern("yyyy-MM-dd E HH:mm:ss") ==> 2009-03-10  20:09:04
 * (new Date()).pattern("yyyy-MM-dd EE hh:mm:ss") ==> 2009-03-10  08:09:04
 * (new Date()).pattern("yyyy-MM-dd EEE hh:mm:ss") ==> 2009-03-10  08:09:04
 * (new Date()).pattern("yyyy-M-d h:m:s.S") ==> 2006-7-2 8:9:4.18
 */
if (!Date.prototype.format) {
  Date.prototype.format = function (fmt) {
    let o = {
      'M+': this.getMonth() + 1, //
      'd+': this.getDate(), //
      'h+': this.getHours() % 12 === 0 ? 12 : this.getHours() % 12, //
      'H+': this.getHours(), //
      'm+': this.getMinutes(), //
      's+': this.getSeconds(), //
      'q+': Math.floor((this.getMonth() + 3) / 3), //
      'S': this.getMilliseconds() //
    }
    let week = {
      '0': '/u65e5',
      '1': '/u4e00',
      '2': '/u4e8c',
      '3': '/u4e09',
      '4': '/u56db',
      '5': '/u4e94',
      '6': '/u516d'
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    if (/(E+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, ((RegExp.$1.length > 1) ? (RegExp.$1.length > 2 ? '/u661f/u671f' : '/u5468') : '') + week[this.getDay() + ''])
    }
    for (let k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
      }
    }
    return fmt
  }
}

if (!Date.prototype.setISO8601) {
  Date.prototype.setISO8601 = function (string) {
    let regexp = '([0-9]{4})(-([0-9]{2})(-([0-9]{2})' +
      '(T([0-9]{2}):([0-9]{2})(:([0-9]{2})(\.([0-9]+))?)?' +
      '(Z|(([-+])([0-9]{2}):([0-9]{2})))?)?)?)?'
    if (string) {
      let d = string.match(new RegExp(regexp))
      let offset = 0
      let date = new Date(d[1], 0, 1)

      if (d[3]) {
        date.setMonth(d[3] - 1)
      }
      if (d[5]) {
        date.setDate(d[5])
      }
      if (d[7]) {
        date.setHours(d[7])
      }
      if (d[8]) {
        date.setMinutes(d[8])
      }
      if (d[10]) {
        date.setSeconds(d[10])
      }
      if (d[12]) {
        date.setMilliseconds(Number('0.' + d[12]) * 1000)
      }
      if (d[14]) {
        offset = (Number(d[16]) * 60) + Number(d[17])
        offset *= ((d[15] === '-') ? 1 : -1)
      }
      offset -= date.getTimezoneOffset()
      let time = (Number(date) + (offset * 60 * 1000))
      this.setTime(Number(time))
    }
  }
}

//jsyyyy-MM-dd
export function simpleDate (d, fallback = null) {
  if (d instanceof Date) {
    return d.format('yyyy-MM-dd')
  } else if (d === null) {
    return fallback
  } else {
    return 'Invalid Date:' + d
  }
}

