//js15:34 2017-04-03
export function humanTime (d, fallback = null) {

  let now = new Date()
  if (d instanceof Date) {

    if (now.toDateString() === d.toDateString()) {
      return d.format('HH:mm')
    } else {
      return d.format('yyyy-MM-dd')
    }
  } else if (d === null) {
    return fallback
  } else {
    return 'Invalid Date:' + d
  }
}

