export function unixTimeStamp2simpleDate (timestamp) {

  if ((typeof timestamp) === 'number') {
    let unixTimestamp = new Date(timestamp)

    return unixTimestamp.format('yyyy-MM-dd')

  } else {

    return 'Invalid timestamp'

  }
}

