import Menu from './Menu'
import { FeatureType } from '../model/feature/FeatureType'

export default class MenuManager {

}

//
MenuManager.refreshByMenus = function (user) {

  let byMenus = []

  if (user.role === 'GUEST') {

    //
    let loginMenu = new Menu('', '/by/user/login', false, 'fa fa-user-circle-o')
    byMenus.push(loginMenu)

  } else {

    //
    let articleListMenu = new Menu('', '/by', false, 'fa fa-book')
    byMenus.push(articleListMenu)

    //
    let tagListMenu = new Menu('', '/by/tag/list', false, 'fa fa-tags')
    byMenus.push(tagListMenu)

    if (user.hasPermission(FeatureType.USER_MANAGE)) {
      //
      let preferenceMenu = new Menu('', '/by/preference', false, 'fa fa-dashboard')
      byMenus.push(preferenceMenu)

      //
      let userMenu = new Menu('', '/by/user/list', false, 'fa fa-user')
      byMenus.push(userMenu)

      //
      let reportMenu = new Menu('', '/by/report/list', false, 'fa fa-warning')
      byMenus.push(reportMenu)
    }


    //
    let logoutMenu = new Menu('', '/by/user/login', false, 'fa fa-power-off')
    byMenus.push(logoutMenu)

  }

  return byMenus
}




